






























    		/*底部*/
.apply[data-v-f0ab9ece]{
				color: white;
}
.footer[data-v-f0ab9ece]{
				position: fixed;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 1rem;
				background: white;
}
.footer ul[data-v-f0ab9ece]{
				list-style: none;
				overflow: hidden;
}
.footer ul li[data-v-f0ab9ece]{
				float: left;
				/*line-height: 50px;*/
				width: 15%;
				margin-left:5%;
				color: #525252;
				font-size: 12px;
}
.footer ul li[data-v-f0ab9ece]:nth-child(4){
			margin: 0;
			width: 40%;
			font-size: 16px;
			font-family: 'macrosoft yahei';
			text-align: center;
			color: white;
			 background: linear-gradient(#35b05d, #41cc6e);
			line-height: 1rem;
}
.footer ul li div[data-v-f0ab9ece]:nth-child(1){
				width: 0.48rem;
				height: 0.48rem;
				margin-top: 0.08rem;
}
.footer ul li:nth-child(1) div[data-v-f0ab9ece]:nth-child(1){
				background: url('../../assets/icon_advisory@2x.png')  no-repeat center/cover;
}
.footer ul li:nth-child(2) div[data-v-f0ab9ece]:nth-child(1){
				background: url('../../assets/icon_phone@2x.png')  no-repeat center/cover;
}
.footer ul li:nth-child(3) div[data-v-f0ab9ece]:nth-child(1){
				background: url('../../assets/icon_Collection@2x.png')  no-repeat center/cover;
}



