
.divide:nth-child(1) {
  height: 0.1rem;
}
.divide {
  height: 0.02rem;
}
.top {
  height: 1.3rem;
/*margin-top: 5px;*/
  background: #fff;
}
.top .left {
  height: 100%;
  width: 1.4rem;
  background: #41cc6e;
  color: #fff;
  padding-top: 0.24rem;
  overflow: hidden;
}
.top .left div {
  color: #fff;
}
.top .left div:nth-child(1) {
  font-weight: 700;
  font-size: 18px;
}
.top .left div:nth-child(2) {
  font-size: 14px;
}
.top .right {
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0.36rem 0.3rem 0 0.3rem;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 100%;
  font-family: 'MicroSoft YaHei';
  font-weight: 600;
}
.top .right div p,
.top .right div span {
  color: #3b3b3b;
}
.top .right p {
  font-size: 11px;
  line-height: 0.22rem;
}
.top .right i {
  width: 0.56rem;
  height: 0.56rem;
  float: left;
  vertical-align: middle;
}
.top .right div {
  float: left;
  margin-left: 0.1rem;
  font-size: 11px;
  line-height: 12px;
  font-weight: 600;
  padding-top: 0.07rem;
}
.top .right div span {
  line-height: 0.22rem;
}
.top .right div:nth-child(1) i {
  background: url("../../assets/icon_Praise@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.top .right div:nth-child(1) i {
    background: url("../../assets/icon_Praise@3x.png") no-repeat center/cover;
}
}
.top .right div:nth-child(2) i {
  background: url("../../assets/icon_Average@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.top .right div:nth-child(2) i {
    background: url("../../assets/icon_Average@3x.png") no-repeat center/cover;
}
}
.top .right div:nth-child(3) i {
  background: url("../../assets/icon_Bad-review@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.top .right div:nth-child(3) i {
    background: url("../../assets/icon_Bad-review@3x.png") no-repeat center/cover;
}
}
.lists {
  padding: 0 0 0.92rem;
  background: #fff;
}
.lists .list {
  margin-bottom: 0.78rem;
  padding: 0.56rem 0.2rem 0 0.2rem;
}
.lists .list .star div {
  overflow: hidden;
}
.lists .list .pic {
  width: 1.1rem;
  float: left;
  overflow: hidden;
  text-align: center;
}
.lists .list .pic p {
  font-size: 11px;
}
.lists .list s {
  display: block;
  height: 1.1rem;
  border-radius: 50%;
  margin-bottom: 0.1rem;
}
.lists .list .word {
  overflow: hidden;
  padding: 0.16rem 0 0 0.4rem;
}
.lists .list .word span,
.lists .list .word p {
  color: #525252;
/*font-size: 12px;*/
}
.lists .list .word span {
  font-size: 13px;
  margin-left: 0.14rem;
  vertical-align: middle;
  display: inline-block;
}
.lists .list .word p {
  font-size: 12px;
  margin-top: 0.3rem;
}
.lists .list .word i {
  display: inline-block;
  width: 0.32rem;
  height: 0.32rem;
  vertical-align: middle;
  margin-right: 0.06rem;
  background: url("../../assets/icon_star_sel@2x.png") no-repeat center/cover;
  vertical-align: middle;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.lists .list .word i {
    background: url("../../assets/icon_star_sel@3x.png") no-repeat center/cover;
}
}
.lists .list .pic p {
  font-size: 11px;
}
.lists .list .res {
  margin: 0.54rem 0.2rem 0 0.2rem;
  padding: 0.3rem 0 0.28rem 0.3rem;
  position: relative;
  font-size: 12px;
  color: #525252;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-radius: 0.2rem;
  background: #f5f5f5;
}
.lists .list .res i {
  position: absolute;
  top: -0.62rem;
  left: 0.3rem;
  border-top-width: 0.32rem;
  border-right-width: 0.15rem;
  border-left-width: 0.15rem;
  border-bottom-width: 0.32rem;
  border-color: transparent;
  border-style: solid;
  border-bottom-color: #f5f5f5;
}
.lists .list:nth-child(1) s {
  background: url("../../assets/touxiang_02.png") no-repeat center/cover;
  border: 1px solid #dbdbdb;
}
.lists .second s {
  background: url("../../assets/touxiang_03.png") no-repeat center/cover;
  border: 1px solid #dbdbdb;
}
.lists .last s {
  background: url("../../assets/touxiang_04.png") no-repeat center/cover;
  border: 1px solid #dbdbdb;
/*background: url('../../../statics/imgs/touxiang_03.png') round;*/
}
.lists .list:nth-child(1) .word i:nth-child(5) {
  background: url("../../assets/icon_star_nor@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.lists .list:nth-child(1) .word i:nth-child(5) {
    background: url("../../assets/icon_star_nor@3x.png") no-repeat center/cover;
}
}
.lists .list:nth-child(1) .word i:nth-child(5) {
  background: url("../../assets/icon_star_nor@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.lists .list:nth-child(1) .word i:nth-child(5) {
    background: url("../../assets/icon_star_nor@3x.png") no-repeat center/cover;
}
}
.divide {
  background: #eaedff;
}


