
.divide {
  height: 0.1rem;
}
.list {
/*margin-top: 5px;*/
/*margin-bottom: 39px;*/
  background: #fff;
  height: 100%;
  padding: 0.72rem 0.4rem 1.2rem 0.2rem;
}
.list .pic {
  width: 1.3rem;
  float: left;
  overflow: hidden;
}
.list s {
  display: block;
  height: 1.3rem;
  background: url("../../assets/touxiang_01.png") round;
  border: 1px solid #dbdbdb;
  border-radius: 50%;
}
.list .word {
  overflow: hidden;
  padding: 0.16rem 0 0 0.4rem;
}
.list .word h4 {
  font-size: 15px;
  color: #3b3b3b;
  font-weight: 700;
}
.list .word span {
  font-size: 13px;
  color: #525252;
}
.list .word p {
  font-size: 12px;
  margin-top: 0.3rem;
  color: #525252;
  margin-bottom: 0.3rem;
}
.list .word .star {
  margin-top: 0.12rem;
}
.list .word .star a {
  font-size: 13px;
}
.list .word .star div {
  overflow: hidden;
}
.list .word .star span {
  vertical-align: middle;
}
.list .word i {
  display: inline-block;
  width: 0.32rem;
  height: 0.32rem;
  margin-right: 0.06rem;
  vertical-align: middle;
  background: url("../../assets/icon_star_sel@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.list .word i {
    background: url("../../assets/icon_star_sel@3x.png") no-repeat center/cover;
}
}
.list .word i:nth-child(5) {
  background: url("../../assets/icon_star_nor@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.list .word i:nth-child(5) {
    background: url("../../assets/icon_star_nor@3x.png") no-repeat center/cover;
}
}
.list .word a {
  color: #2e8ae8;
  font-size: 13px;
  text-decoration: none;
}


