
.tool {
  font-family: 'MicroSoft YaHei';
  color: #525252;
}
.tool header a {
  color: #fff !important;
}
.tool header a i {
  font: 700 16px/34px simsun;
}
.tool .mint-header {
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 13;
}
.tool .mui-bar {
  position: relative;
/*-webkit-box-shadow: none;*/
  box-shadow: none;
  background: #fff;
  height: 0.8rem;
  margin-top: 40px;
  padding: 0 0.2rem;
  padding-right: 1rem;
  overflow: hidden;
}
.tool .mui-bar .tab::-webkit-scrollbar {
  display: none;
}
.tool .mui-bar .tab {
  width: 5.2rem;
  overflow-x: scroll;
}
.tool .mui-bar .tab div {
  width: 5.6rem;
}
.tool .mui-bar input {
  transition: all 1s;
  position: absolute;
  top: 0.1rem;
  left: 0.1rem;
  line-height: 0.6rem;
  height: 0.6rem;
  width: 5.2rem;
  border-radius: 0.3rem;
  outline: none;
  border: none;
  border: 1px solid #35b05d;
  text-indent: 0.2rem;
  -webkit-transform: translate(5.6rem);
          transform: translate(5.6rem);
}
.tool .mui-bar .search {
  position: absolute;
  height: 100%;
  width: 1rem;
  box-shadow: -7px -2px 12px #e5dfff;
  right: 0;
  top: 0;
  background: #fff;
  text-align: center;
  padding-top: 0.2rem;
}
.tool .mui-bar .search i {
  display: inline-block;
  height: 0.4rem;
  width: 0.4rem;
  background: url("../../assets/shaixuan-icon@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.tool .mui-bar .search i {
    background: url("../../assets/shaixuan-icon@3x.png") no-repeat center/cover;
}
}
.tool .mui-bar .router-link-active span {
  background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#35b05d), to(#41cc6e));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.tool .mui-bar .router-link-active span::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, #41cc6e, #35b05d);
}
.tool .mui-tab-item {
  padding: 0 0.05rem;
  padding-bottom: 2px;
  position: relative;
  width: 20%;
  float: left;
  text-align: center;
  height: 100%;
  text-decoration: none;
}
.tool .mui-tab-item span {
  display: block;
  position: relative;
  font-size: 15px;
/*width: 100%;
                height: 100%;*/
  line-height: 0.8rem;
  font-family: '微软雅黑';
  background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#9b9b9b), to(#6b6b6b));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.tool .main {
  margin-top: 0.1rem;
}
.tool .main .item:nth-child(1) i {
  background: url("../../assets/logo_1.png") no-repeat center/cover;
}
.tool .main .item:nth-child(2) i {
  background: url("../../assets/logo_2.png") no-repeat center/cover;
}
.tool .main .item:nth-child(3) i {
  background: url("../../assets/logo_3.png") no-repeat center/cover;
}
.tool .main .item {
  margin-bottom: 0.02rem;
  background: #fff;
  padding: 0.6rem 0.4rem 0.6rem 0.2rem;
}
.tool .main .item .summary {
  transition: all 1s;
  position: relative;
  overflow: hidden;
  margin-bottom: 5px;
  line-height: 22px;
  word-break: break-all;
  font-size: 12px;
  line-height: 0.4rem;
/*text-indent:5em;*/
}
.tool .main .item .summary p {
  margin-top: 0.12rem;
  font-size: 12px;
  line-height: 0.4rem;
  display: inline-block;
}
.tool .main .item .packup p {
  height: 0.7rem;
  overflow: hidden;
}
.tool .main .item .packup:before {
  display: block;
  content: attr(data-content);
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  height: 0.4rem;
  width: 100%;
  overflow: hidden;
  background-color: #fff;
  margin-top: 0.12rem;
  font-size: 12px;
  line-height: 0.4rem;
}
.tool .main .item .packup:after {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  box-sizing: border-box;
  -webkit-line-clamp: 2;
  content: attr(data-content);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  text-indent: -6em;
  padding-right: 6em;
  background-color: #fff;
  margin-top: 0.12rem;
  font-size: 12px;
  line-height: 0.4rem;
}
.tool .main .item i {
  height: 1.5rem;
  width: 1.5rem;
  float: left;
  overflow: hidden;
  margin-top: 3px;
}
.tool .main .item .right {
  overflow: hidden;
  padding-left: 0.4rem;
}
.tool .main .item .right .fl h4 {
  font-size: 15px;
  color: #3b3b3b;
  font-weight: bold;
}
.tool .main .item .right .fl h5 {
  font-size: 13px;
  margin-top: 0.08rem;
}
.tool .main .item .right .fr {
  width: 1.24rem;
  height: 0.44rem;
  line-height: 0.4rem;
  text-align: center;
  font-size: 12px;
  color: #35b05d;
  border: 1px solid #35b05d;
  border-radius: 0.2rem;
  margin-top: 0.1rem;
}
.tool .main .item .right span {
  font-size: 11px;
  color: #9b9b9b;
  position: absolute;
  right: 0;
  bottom: 4px;
  z-index: 6;
}
.tool .main .item .right span s {
  width: 0.25rem;
  height: 0.25rem;
  display: inline-block;
  background: url("../../assets/shouqi-icon@2x.png") no-repeat center/cover;
  vertical-align: middle;
  margin-bottom: 1px;
  margin-left: 0.1rem;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.tool .main .item .right span s {
    background: url("../../assets/shouqi-icon@3x.png") no-repeat center/cover;
}
}
.tool .main .item .right span .change {
  background: url("../../assets/zhankai-icon@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.tool .main .item .right span .change {
    background: url("../../assets/zhankai-icon@3x.png") no-repeat center/cover;
}
}


