
body,
html {
  height: 100%;
}
.apply {
  font-family: 'MicroSoft YaHei';
}
.apply header a {
  color: #fff !important;
}
.apply header a i {
  font: 700 16px/34px simsun;
}
.apply .mint-header {
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 13;
}
.apply .main {
  height: 100%;
  padding: 40px 10px 0;
  color: #525252;
}
.apply .main .indent {
  border-radius: 0.3rem;
  margin-top: 0.2rem;
  overflow: hidden;
  box-shadow: 0px 16px 16px #e5dfff;
}
.apply .main .indent .top {
  height: 2.3rem;
  background: #fff;
  padding: 0 0.3rem;
  padding-top: 0.4rem;
}
.apply .main .indent .top i {
  float: left;
  width: 2rem;
  height: 1.5rem;
  background: url("../../assets/baoming-6@2x.png") no-repeat center/cover;
  margin-right: 0.3rem;
}
.apply .main .indent .top .right1 {
  overflow: hidden;
  margin-left: 0.3rem;
  margin-top: 0.2rem;
}
.apply .main .indent .top .right1 span {
  float: left;
  width: 0.58rem;
  height: 0.32rem;
  background: url("../../assets/title_free@2x.png") no-repeat center/cover;
  vertical-align: middle;
  margin-top: 3px;
  margin-right: 0.1rem;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.apply .main .indent .top .right1 span {
    background: url("../../assets/title_free@3x.png") no-repeat center/cover;
}
}
.apply .main .indent .top .right1 h4 {
  font-size: 15px;
  font-weight: bold;
  color: #3b3b3b;
}
.apply .main .indent .top .right1 p {
  font-size: 13px;
  margin-top: 0.1rem;
}
.apply .main .indent .flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  height: 1.4rem;
  background: #fff;
  padding: 0 0.3rem 0;
  height: 1.5rem;
  margin-top: 0.02rem;
}
.apply .main .indent .flex .sp1 {
  font-size: 13px;
  line-height: 1.4rem;
}
.apply .main .indent .flex .sp2 {
  font-size: 15px;
  font-weight: bold;
  color: #3b3b3b;
  vertical-align: middle;
  line-height: 1.4rem;
}
.apply .main .indent .flex .sp2 i {
  width: 0.34rem;
  height: 0.34rem;
  display: inline-block;
  border-radius: 50%;
  vertical-align: middle;
  margin-left: 0.2rem;
  background: url("../../assets/zhifuchenggong@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.apply .main .indent .flex .sp2 i {
    background: url("../../assets/zhifuchenggong@3x.png") no-repeat center/cover;
}
}
.apply .main .next {
  position: absolute;
  width: 94%;
  bottom: 0.2rem;
  height: 1.4rem;
  line-height: 1.4rem;
  border-radius: 0.2rem;
  color: #fff;
  text-align: center;
  background: linear-gradient(-90deg, #35b05d, #41cc6e);
}


