
.pay {
  font-family: 'MicroSoft YaHei';
}
.pay header a {
  color: #fff !important;
}
.pay header a i {
  font: 700 16px/34px simsun;
}
.pay .mint-header {
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 13;
}
.pay .main {
  padding: 40px 10px 0;
}
.pay .main .top {
  line-height: 0.6rem;
  height: 0.6rem;
  background: #fffbd9;
  box-shadow: 0px 16px 16px #e5dfff;
  border-bottom-right-radius: 0.2rem;
  border-bottom-left-radius: 0.2rem;
  padding-left: 0.3rem;
}
.pay .main .top i {
  display: inline-block;
  width: 0.3rem;
  height: 0.3rem;
  vertical-align: middle;
  background: url("../../assets/shijian@2x.png") no-repeat center/cover;
  margin-right: 0.1rem;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.pay .main .top i {
    background: url("../../assets/shijian@3x.png") no-repeat center/cover;
}
}
.pay .main .top span {
  font-size: 11px;
}
.pay .main .top span s {
  text-decoration: none;
  color: #fa870c;
}
.pay .main .indent {
  border-radius: 0.3rem;
  margin-top: 0.2rem;
  overflow: hidden;
  box-shadow: 0px 16px 16px #e5dfff;
}
.pay .main .indent div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  height: 1.4rem;
  background: #fff;
  margin-bottom: 0.02rem;
  padding: 0 0.15rem 0;
}
.pay .main .indent div span {
  font-size: 13px;
  line-height: 1.4rem;
}
.pay .main .indent div span:nth-child(2) {
  font-size: 14px;
  font-weight: 700;
}
.pay .main .chose {
  margin-top: 0.1rem;
  box-shadow: 0px 16px 16px #e5dfff;
  border-radius: 0.3rem;
  overflow: hidden;
}
.pay .main .chose div:nth-child(2) i {
  background: url("../../assets/alipay@2x.png") no-repeat center/cover;
  width: 0.64rem;
  margin-right: 0.1rem;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.pay .main .chose div:nth-child(2) i {
    background: url("../../assets/alipay@3x.png") no-repeat center/cover;
}
}
.pay .main .chose div {
  height: 1.4rem;
  line-height: 1.4rem;
  background: #fff;
  margin-bottom: 0.02rem;
  padding: 0 0.15rem 0;
}
.pay .main .chose div span {
  font-size: 13px;
}
.pay .main .chose div span:nth-child(2) {
  font-size: 14px;
}
.pay .main .chose div i {
  display: inline-block;
  width: 0.58rem;
  height: 0.5rem;
  vertical-align: middle;
  background: url("../../assets/wechat@2x.png") no-repeat center/cover;
  margin-right: 0.19rem;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.pay .main .chose div i {
    background: url("../../assets/wechat@3x.png") no-repeat center/cover;
}
}
.pay .main .chose div s {
  width: 0.3rem;
  height: 0.3rem;
  float: right;
  margin-top: 0.54rem;
  border-radius: 50%;
}
.pay .main .chose div .check {
  background: url("../../assets/zhifuchenggong@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.pay .main .chose div .check {
    background: url("../../assets/zhifuchenggong@3x.png") no-repeat center/cover;
}
}
.pay .main .chose div .uncheck {
  border: 1px solid #9b9b9b;
}
.pay .main .chose .second i {
  background: url("../../assets/alipay@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.pay .main .chose .second i {
    background: url("../../assets/alipay@3x.png") no-repeat center/cover;
}
}
.pay .main .affirm {
  margin-top: 1rem;
  height: 1.4rem;
  border-radius: 0.2rem;
  text-align: center;
  background: linear-gradient(-90deg, #35b05d, #41cc6e);
}
.pay .main .affirm a {
  display: block;
  color: #fff;
  line-height: 1.4rem;
}


