
.common-star {
  display: inline-block;
  font-size: 0;
}
.common-star .star-item {
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 0.24rem 0.24rem;
  width: 0.24rem;
  height: 0.24rem;
  margin-right: 0.06rem;
}
.common-star .star-item.on {
  background: url("../../assets/star-jihuo@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.common-star .star-item.on {
    background: url("../../assets/star-jihuo@3x.png") no-repeat center/cover;
}
}
.common-star .star-item.off {
  background: url("../../assets/star@2x.png") no-repeat center/cover;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.common-star .star-item.off {
    background: url("../../assets/star@3x.png") no-repeat center/cover;
}
}


