
.result {
  font-family: 'MicroSoft YaHei';
  margin: 1.4rem 0.6rem 0 0.6rem;
  color: #525252;
}
.result header a {
  color: #fff !important;
}
.result header a i {
  font: 700 16px/34px simsun;
}
.result .mint-header {
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 13;
}
.result .main .success {
  box-shadow: 0px 16px 16px #e5dfff;
  overflow: hidden;
  border-radius: 0.2rem;
}
.result .main .success div {
  margin-top: 0.02rem;
  background: #fff;
}
.result .main .success div:nth-child(1) {
  text-align: center;
  margin: 0;
}
.result .main .success div:nth-child(1) i {
  display: inline-block;
  width: 1.48rem;
  height: 1.48rem;
  background: url("../../assets/zhifuchenggong@2x.png") no-repeat center/cover;
  border-radius: 50%;
  box-shadow: 0px 16px 16px #e5dfff;
  margin: 0.6rem 0 0.4rem 0;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.result .main .success div:nth-child(1) i {
    background: url("../../assets/zhifuchenggong@3x.png") no-repeat center/cover;
}
}
.result .main .success div:nth-child(1) p {
  font-size: 15px;
  font-weight: bold;
  color: #3b3b3b;
  padding-bottom: 0.52rem;
}
.result .main .success div:nth-child(2),
.result .main .success div:nth-child(3) {
  height: 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0 0.3rem;
}
.result .main .success div:nth-child(2) span:nth-child(1),
.result .main .success div:nth-child(3) span:nth-child(1) {
  font-size: 13px;
  line-height: 1rem;
}
.result .main .success div:nth-child(2) span:nth-child(2),
.result .main .success div:nth-child(3) span:nth-child(2) {
  font-size: 15px;
  color: #3b3b3b;
  font-weight: bold;
  line-height: 1rem;
}
.result .main .success div:nth-child(4) {
  height: 2.8rem;
  text-align: center;
  padding-top: 0.4rem;
}
.result .main .success div:nth-child(4) s {
  width: 2rem;
  height: 2rem;
  display: inline-block;
  background: url("../../assets/qcode@2x.png") no-repeat center/cover;
  border: 3px solid #4aceff;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.result .main .success div:nth-child(4) s {
    background: url("../../assets/qcode@3x.png") no-repeat center/cover;
}
}
.result .main .success div:nth-child(5) {
  text-align: center;
  height: 0.8rem;
  font-size: 11px;
}
.result .main .success div:nth-child(5) p {
  line-height: 0.8rem;
}
.result .main .check {
  margin-top: 0.1rem;
  height: 1.1rem;
  line-height: 1.1rem;
  border-radius: 0.2rem;
  text-align: center;
  box-shadow: 0px 16px 16px #e5dfff;
  background: #fff;
}
.result .main .check a {
  background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#41cc6e), to(#35b05d));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bold;
}


